import React, { Component } from 'react'
import { scaleLinear } from 'd3-scale'
import { max } from 'd3-array'
import { axisLeft } from 'd3-axis';
import { select } from 'd3-selection'
import '../App.css'

class BarChart extends Component {

  componentDidMount() {
    this.createBarChart()
  }
  componentDidUpdate() {
    this.createBarChart()
  }

  createBarChart = async () => {
    const [height, width] = this.props.size
    const { data } = this.props
    if (!data) return (<div>No data</div>)
    const values = data.map(d => d.value)
    const names = data.map(d => d.text)

    const node = this.node
    const yRange = scaleLinear()
      .range([height, 0])
      .domain([0, Math.max(...values)])
      .nice();
    const dataMax = max(values)
    const yScale = scaleLinear()
      .domain([0, dataMax])
      .range([0, height])



    select(node)
      .selectAll('rect')
      .data(data)
      .enter()
      .append('rect')

    select(node)
      .selectAll('rect')
      .data(values)
      .exit()
      .remove()

    select(node)
      .selectAll('text')
      .remove()

    select(node)
      .selectAll('.axisSteelBlue')
      .remove()

    select(node)
      .selectAll('rect')
      .data(values)
      .style('fill', 'white')
      .style('stroke', "black")
      .style('stroke-width', "3")
      .attr('x', (d, i) => i * (width / values.length)+50)
      .attr('y', d => height - yScale(d))
      .attr('height', d => yScale(d))
      .attr('width', (width / values.length) / 1.5)

    select(node)
      .selectAll('text')
      .data(names)
      .enter()
      .append("text")
      .text(d => d)
      .style('fill', 'black')
      .attr("x", (d, i) => -295)
      .attr("y", (d, i) => (i * 30) + 62)
      .attr("font-size", "11px")
      .attr('transform', 'rotate(-90)')

    select(node).append("g")
      .attr("class", "axisSteelBlue")
      .attr("transform", "translate(50)")
      .call(axisLeft(yRange));

  }
  render() {
    const [height, width] = this.props.size
    return <svg ref={node => this.node = node} width={width+50} height={height} style={{padding:"20px"}}></svg>
  }
}
export default BarChart


