import React, { Component } from "react"
import {
  ComposableMap,
  ZoomableGroup,
  Geographies,
  Geography
} from "react-simple-maps"

const wrapperStyles = {
  width: "100%",
  maxWidth: 980,
  margin: "0 auto",
}

class USAMap extends Component {
  render() {
    return (
      <div style={wrapperStyles}>
        <ComposableMap
          projectionConfig={{ scale: 600 }}
          width={500}
          height={500}
          style={{
            float: "left",
            width: "67%",
            height: "auto",
          }}>
          <ZoomableGroup disablePanning center={[270, 35]}>
            <Geographies geography="/04-mapsandviews/data/us-states-20m.json">
              {(geographies, projection) =>
                geographies.map((geo, i) => (
                  <Geography
                    key={i}
                    geography={geo}
                    projection={projection}
                    onClick={(g, e) => {
                      this.props.onStateClicked(g.properties.iso_3166_2)
                    }}
                    style={{
                      default: {
                        fill: "#ECEFF1",
                        stroke: "#607D8B",
                        strokeWidth: 0.75,
                        outline: "none",
                      },
                      hover: {
                        fill: "#CFD8DC",
                        stroke: "#607D8B",
                        strokeWidth: 0.75,
                        outline: "none",
                      },
                      pressed: {
                        fill: "#FF5722",
                        stroke: "#607D8B",
                        strokeWidth: 0.75,
                        outline: "none",
                      },
                    }}
                  />
                ))
              }
            </Geographies>
          </ZoomableGroup>
        </ComposableMap>
      </div>
    )
  }
}

export default USAMap


