import React, { Component } from 'react';
import BarChart from './charts/BarChart';
import WordCloud from 'react-d3-cloud';
import USAMap from './charts/USAMap';
import Slider from 'rc-slider';
import 'rc-slider/assets/index.css';
import './App.css';

class App extends Component {
  constructor() {
    super()
    this.state = {
      data: null,
      year: 2000,
      usState: "MA"
    }
  }

  loadData = async (state, year) => {
    let data = await fetch(`/04-mapsandviews/data/${state}.csv.json`)
      .then(res => res.json())
      .then(res => res.filter(d => Number.parseInt(d.Year, 10) === year).slice(0, 10))
      .then(year => year.map(y => {
        return {
          text: y.Name,
          value: Number.parseInt(y.Occurence, 10)
        }
      }))
    this.setState({ data })
  }

  componentDidMount() {
    const { year, usState } = this.state

    this.loadData(usState, year)
  }

  componentWillUpdate(nextProps, nextState) {
    const { year, usState } = this.state
    if ((year !== nextState.year)
      || (usState !== nextState.usState)) {
      this.loadData(nextState.usState, nextState.year)
    }
  }

  range = (start, count) => {
    let o = {}
    Array.apply(0, Array(count))
      .forEach((element, index) => {
        if(index%10 === 0) o[index + start] = index + start;
      });
      console.log(o)
    return o
  }

  render() {
    const { data, year, usState } = this.state
    const charts = data ? (
      <div>
        <USAMap
          onStateClicked={usState => this.setState({ usState })} />
        <div width="30%" style={{ float: "right" }}>
          <BarChart
            data={data}
            size={[300, 300]} />
          <WordCloud
            height="300"
            width="300"
            data={data.map(d => {
              return {
                text: d.text,
                value: 1 + (d.value / Math.max(...data.map(d => d.value)))
              }
            })}
            fontSizeMapper={word => Math.pow(word.value, 5) * 2} />
        </div>
      </div>) : <div>No Data</div>

    return (
      <div className="App" >
        <header className="App-header">
          <h1 className="App-title">{`Top 10  Names for ${usState} Babies Born in ${year}`}</h1>
        </header>
        <div>
          <Slider style={{
            marginLeft: "32px",
            marginRight: "16px",
            maxWidth: "700px"
          }}
            min={1910}
            max={2016}
            defaultValue={1910}
            onAfterChange={v => this.setState({ year: Number.parseInt(v, 10) })}
            marks={this.range(1910, 106)} />
        </div>

        {charts}
      </div >
    );
  }
}

export default App;



